export default {
  name: 'Placeholder',
  props: {
    ready: {
      type: Boolean,
      default: false,
    },
    width: {
      type: String,
      default: '50px',
    },
    height: {
      type: String,
      default: '1.2em',
    },
  },
  computed: {
    placeholderStyle() {
      return {
        backgroundColor: this.$themeTokens.textDisabled,
        width: this.width,
        height: this.height,
      };
    },
  },
};

