import { coachStringsMixin } from './commonCoachStrings';

export default {
  name: 'LessonActive',
  mixins: [coachStringsMixin],
  props: {
    active: {
      type: Boolean,
      required: true,
    },
  },
  computed: {
    label() {
      return this.active ? this.$tr('lessonActiveLabel') : this.$tr('lessonInactiveLabel');
    },
  },
  $trs: {
    lessonActiveLabel: 'Active',
    lessonInactiveLabel: 'Inactive',
  },
};

