import commonCoach from '../../common';
import ProgressSummaryBar from '../../common/status/ProgressSummaryBar';
import StatusSummary from '../../common/status/StatusSummary';

export default {
  name: 'StatusTestPage',
  components: {
    StatusSummary,
    ProgressSummaryBar,
  },
  mixins: [commonCoach],
  data() {
    return {
      testSummaries: [
        {
          name: 'all started',
          started: 12,
          notStarted: 0,
          completed: 0,
          helpNeeded: 0,
        },
        {
          name: 'none started',
          started: 0,
          notStarted: 12,
          completed: 0,
          helpNeeded: 0,
        },
        {
          name: 'all completed',
          started: 0,
          notStarted: 0,
          completed: 12,
          helpNeeded: 0,
        },
        {
          name: 'all need help',
          started: 0,
          notStarted: 0,
          completed: 0,
          helpNeeded: 12,
        },
        {
          name: 'some started',
          started: 6,
          notStarted: 6,
          completed: 0,
          helpNeeded: 0,
        },
        {
          name: 'some completed',
          started: 0,
          notStarted: 6,
          completed: 6,
          helpNeeded: 0,
        },
        {
          name: 'some completed, others need help',
          started: 0,
          notStarted: 0,
          completed: 6,
          helpNeeded: 6,
        },
        {
          name: 'all started, some need help',
          started: 6,
          notStarted: 0,
          completed: 0,
          helpNeeded: 6,
        },
        {
          name: 'some completed, some started',
          started: 4,
          notStarted: 4,
          completed: 4,
          helpNeeded: 0,
        },
        {
          name: 'some completed, some need help',
          started: 0,
          notStarted: 4,
          completed: 4,
          helpNeeded: 4,
        },
        {
          name: 'some started, some need help',
          started: 4,
          notStarted: 4,
          completed: 0,
          helpNeeded: 4,
        },
        {
          name: 'some completed, some started, others need help',
          started: 4,
          notStarted: 0,
          completed: 4,
          helpNeeded: 4,
        },
        {
          name: 'some completed, some started, some need help',
          started: 3,
          notStarted: 3,
          completed: 3,
          helpNeeded: 3,
        },
      ],
      started: 3,
      completed: 0,
      notStarted: 10,
      helpNeeded: 1,
    };
  },
  computed: {
    movingTally() {
      return {
        completed: this.completed,
        notStarted: this.notStarted,
        started: this.started,
        helpNeeded: this.helpNeeded,
      };
    },
  },
  mounted() {
    this.update();
  },
  methods: {
    update() {
      if (this.notStarted === 0) {
        this.notStarted = 12;
        this.completed = 0;
        this.started = 3;
        this.helpNeeded = 0;
      } else {
        this.notStarted -= 2;
        this.completed += 1;
        this.started += 1;
        this.helpNeeded = this.helpNeeded ? 0 : this.started;
      }
      setTimeout(this.update, 5000);
    },
  },
};

