import { coachStringsMixin } from './commonCoachStrings';

export default {
  name: 'QuizActive',
  mixins: [coachStringsMixin],
  props: {
    active: {
      type: Boolean,
      required: true,
    },
  },
  computed: {
    label() {
      return this.active ? this.$tr('activeQuizLabel') : this.$tr('inactiveQuizLabel');
    },
  },
  $trs: {
    activeQuizLabel: 'Active',
    inactiveQuizLabel: 'Inactive',
  },
};

